.navInfo {
  min-height: 90px;
  display: flex;
  align-items: center;
  border-top: 1px solid rgba(112, 112, 112, .3); }

.copyright {
  @include flex-center();
  height: 70px;
  background-color: $blue;
  color: white;
  font-size: 14px; }

@include media-breakpoint-down(xs) {
  .footer--info {
    justify-content: center;
    &:first-child {
      margin-bottom: 10px; } }
  .navInfo {
    padding: 10px 0; } }
