.topLine {
  background-color: $blue;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 0;
  position: fixed;
  //z-index: 1
  left: 0;
  top: 0;
  &Container {
    display: block;
    justify-content: space-between;
    align-items: center;
    height: 45px; } }
.topMenu {
  text-transform: uppercase;
  @include font(PT_Sans, $sz: 14px);
  letter-spacing: 1px;
  height: 45px;
  &__list {
    display: flex;
    list-style-type: none;
    height: 100%;
    margin: 0;
    padding: 0; }
  &__item {
    @include ts(.3, bgc);
    background-color: white;
    cursor: pointer;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid rgba(112, 112, 112, 0.3);
    &:hover {
      background-color: $accent;
      .topMenu__link {
        text-decoration: none;
        color: white; } } }
  &__link {
    line-height: 45px;
    color: #000;
    margin-right: 7px;
    @include ts(.3, c);
    i {
      margin-left: 10px; } }
  &__item--active {
    background-color: $accent;
    .topMenu__link {
      color: white; } } }

@include media-breakpoint-up(md) {
  .topLine {
    padding-bottom: 0;
    padding-top: 0; } }
