$animationTime: .3;
.itemOverlay {
  padding: 15px;
  @include stretch();
  @include flex-center();
  background-color: #333;
  opacity: 0;
  @include ts($animationTime);
  &--radius {
    border-radius: 30px;
    &:before {
      border-radius: 25px; } }
  &:before {
    content: '';
    @include stretch();
    border: 1px solid white;
    @include ts($animationTime); }
  &__text {
    text-align: center;
    color: $accent;
    @include font(PinkBlue, $sz: 50px);
    letter-spacing: 3px;
    line-height: 1.2; }
  &--radius {
    border-radius: 30px;
    &:before {
      border-radius: 21px; } }
  &--hot {
    @include stretch(10px);
    @include font(PTSans);
    @include flex-center();
    flex-direction: column;
    color: #ffffff;
    justify-content: space-around;
    .itemOverlay {
      &__shopName {
        color: $accent;
        @include font(PinkBlue, $sz: 25px);
        align-self: flex-start;
        margin-left: 10px;
        margin-bottom: 10px;
        margin-top: 5px; }
      &__name {
        @include font(ProximaNova, $s: italic, $w: bold, $sz: 25px); }
      &__text {
        font-size: 12px;
        color: darken(#fff, 15%);
        font-style: italic;
        line-height: 1.5;
        align-self: flex-end;
        padding: 0 20px; }
      &__cost {
        align-self: flex-end;
        font-weight: bold;
        font-size: 22px;
        color: $blue;
        margin-bottom: 10px;
        margin-right: 10px;
        >span {
          font-style: italic;
          margin-left: 3px; } } } } }

@include media-breakpoint-up(lg) {
  .itemOverlay {
    &--hot {
      justify-content: flex-start; } } }
@media (max-width: 490px) {
  .itemOverlay--radius {
    border-radius: 15px;
    &:before {
      border-radius: 7px; } } }
