.hamburger {
  display: none;
  align-items: center; }
// ≥768px
@include media-breakpoint-down(sm) {
  .topMenu {
    display: none; }
  .hamburger {
    display: inline-flex; } }

.mobileMenu {
  display: none; }
.mm {
  &-listitem {
    @include font(PTSans);
    text-transform: uppercase;
    color: #ffffff;
    @include ts();
    &:hover,
    &_selected {
      background: rgba(white, .2);
      color: #fff; } } }
