.foodMenu {
  width: 100%;
  &__list {
    list-style-type: none; }
  &__item {
    @include main-bd();
    padding: 7px;
    @include ts();
    &:hover {
      .foodMenu__link {
        text-decoration: none;
        color: #000000; }
      background-color: rgba(#000, .1);
      cursor: pointer; } }
  &__link {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #000;
    text-transform: uppercase; }
  &__icon {
    max-width: 30px;
    max-height: 30px;
    display: block;
    margin-right: 10px; }
  &--mobile {
    position: fixed;
    right: -300px;
    display: block;
    width: 300px;
    z-index: 10;
    @include ts(0.5);
    &>.foodMenu__list {
      background-color: #fff;
      padding: 0; } } }

.menuButton {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: $accent;
  z-index: 11;
  @include ts(0.5);
  @include flex-center();
  .fa-clipboard-list {
    height: 30px;
    width: 30px;
    margin-bottom: 2px;
    path {
      fill: white; } }
  &--fixed {
    position: fixed;
    bottom: 20px; }
  &--hide {
    right: -100px; }
  &__container {
    position: relative; } }
