::placeholder {
  color: #666; }

::selection {
  background-color: $accent;
  color: #fff; }

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: $PT;
  overflow-x: hidden;
  color: $text;
  background-color: white;
  input, textarea {
    border: #666 1px solid;
    outline: none;
    &:focus:required:invalid {
      border-color: red; }
    &:required:valid {
      border-color: green; } } }

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
  &.img-width {
    width: 100%; } }

.text-center {
  text-align: center; }
.container-fluid {
  padding: 0; }
b, strong {
  font-weight: bold; }

.owl-carousel {
  .owl-nav {
    font-size: 65px;
    color: #ffffff;
    line-height: 100%;
    font-weight: 900;
    .owl-prev, .owl-next {
      position: absolute;
      top: 0;
      bottom: 0;
      outline: none;
      @include ts(.4, o);
      &:hover {
        opacity: .6; } }
    .owl-prev {
      left: 50px; }
    .owl-next {
      right: 50px; } } }
.head__img {
  min-height: 350px;
  max-height: 500px;
  width: 100%;
  background-position: center;
  background-size: cover; }
